@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;500;700&display=swap);
.search-container {
  position: relative;
  max-width: 100%;
  height: 37.5rem;
  overflow: hidden;
  background-color: #121212;
}

.search-container img {
  position: absolute;
  max-width: 100%;
  margin-top: -20%;
}

.search-container > .search-bar-area {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100%;
  position: relative;
  z-index: 2;
  padding-left: 200px;
  padding-right: 200px;
}
.search-container > .search-bar-area > div {
  width:100%;
  margin-bottom: 50px;
}

.search-container > .search-bar-area > div > span {
  font-size: 4.125rem;
  font-weight: 700;
  color: white;
  margin: 0;
}

.search-bar {
  width: 100%;
  height: 54px;
  padding-left: 15px;
  margin-top: 20px;
  border-radius: 4px;
  border: none;
  background-color: #f5f5f5;
}

.search-bar-input-box {
  width: 700px;
}

.search-bar-text {
  font-family: -apple-system, BlinkMacSystemFont, San Francisco, Helvetica Neue,
    Helvetica, Ubuntu, Roboto, Noto, Segoe UI, Arial, sans-serif;
  font-size: 15px;
  font-weight: 400;
  line-height: 1.6;
  color: #111;
}

.search-bar:focus {
  outline: none;
}

@media(max-width: 790px) {
  .search-container > .search-bar-area {
    padding-left: 100px;
    padding-right: 100px;
  }
}

.image-buttons {
  position: absolute;
}

.image-buttons-container {
  max-width: 1280px;
  z-index:2;
}

.image-shadow {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-image: linear-gradient(
    180deg,
    rgba(0, 0, 0, 0.2) 0,
    rgba(0, 0, 0, 0.199) 3.5%,
    rgba(0, 0, 0, 0.195) 7%,
    rgba(0, 0, 0, 0.19) 10.35%,
    rgba(0, 0, 0, 0.182) 13.85%,
    rgba(0, 0, 0, 0.174) 17.35%,
    rgba(0, 0, 0, 0.165) 20.85%,
    rgba(0, 0, 0, 0.155) 24.35%,
    rgba(0, 0, 0, 0.145) 27.85%,
    rgba(0, 0, 0, 0.135) 31.35%,
    rgba(0, 0, 0, 0.126) 34.85%,
    rgba(0, 0, 0, 0.118) 38.35%,
    rgba(0, 0, 0, 0.11) 41.85%,
    rgba(0, 0, 0, 0.105) 45.35%,
    rgba(0, 0, 0, 0.1) 48.85%,
    rgba(0, 0, 0, 0.103) 52.35%,
    rgba(0, 0, 0, 0.112) 55.85%,
    rgba(0, 0, 0, 0.126) 59.35%,
    rgba(0, 0, 0, 0.144) 62.85%,
    rgba(0, 0, 0, 0.165) 66.35%,
    rgba(0, 0, 0, 0.188) 69.85%,
    rgba(0, 0, 0, 0.213) 73.35%,
    rgba(0, 0, 0, 0.237) 76.85%,
    rgba(0, 0, 0, 0.262) 80.35%,
    rgba(0, 0, 0, 0.285) 83.85%,
    rgba(0, 0, 0, 0.306) 87.35%,
    rgba(0, 0, 0, 0.324) 90.85%,
    rgba(0, 0, 0, 0.338) 94.35%,
    rgba(0, 0, 0, 0.347) 97.85%,
    rgba(0, 0, 0, 0.35)
  );
  pointer-events: none;
  margin-bottom: 15px;
}

.image-buttons button {
  font-size: 0.833rem;
  color: #767676;
  border-radius: 4px;
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.06);
  border: 10px solid hsla(0, 0%, 100%, 0.9);
  background-color: hsla(0, 0%, 100%, 1);
  cursor: pointer;
}

.image-buttons button:hover {
  color: #111;
  background-color: #fff;
}

.image-buttons button:hover {
  color: #111;
  background-color: #fff;
}

.image-buttons-top {
  display: flex;
  width: 100%;
  height: 32px;
  top: 20px;
}

.image-buttons-top > button:first-child {
  margin-left: auto;
  margin-right: 15px;
}

.image-buttons-top > button:last-child {
  margin-right: 15px;
}

.image-buttons-top > button:last-child:hover {
  cursor: pointer;
}

.image-buttons-bottom {
  display: flex;
  justify-content: space-between;
  width: 100%;
  height: 32px;
  bottom: 30px;
}

.image-buttons-bottom > button {
  margin-right: 15px;
  margin-left: 15px;
}

.image-buttons-profile {
  position: relative;
  display: inline-flex;
  line-height: 1;
  overflow: hidden;
  vertical-align: middle;
  margin-right: 5px;
  margin-left: 15px;
}

.image-buttons-profile > div {
  width: 100%;
  margin-right: 5px;
}

.image-buttons-profile > div > img {
  width: 32px;
  height: 32px;
  border-radius: 50%;
}

.image-buttons-profile-username {
  color: white;
  font-weight: bold;
  line-height: 32px;
}

.picture {
  position: relative;
}

.unsplash-img {
  width: 100%;
  vertical-align: middle;
  padding-bottom: 15px;
  cursor: zoom-in;
}

.show-up {
  -webkit-animation-name: fadeInBottom;
          animation-name: fadeInBottom;
  -webkit-animation-duration: 2.5s;
          animation-duration: 2.5s;
  -webkit-animation-fill-mode: both;
          animation-fill-mode: both;
  transform: translateY(0%);
}

@-webkit-keyframes fadeInBottom {
  from {
    opacity: 0;
    transform: translateY(100%);
  }
  to {
    opacity: 1;
  }
}

@keyframes fadeInBottom {
  from {
    opacity: 0;
    transform: translateY(100%);
  }
  to {
    opacity: 1;
  }
}

.image-modal-portal {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.6);
  cursor: zoom-out;
  overflow: auto;
  z-index: 999;
}

.image-modal {
  display: flex;
  padding-top: 32px;
  padding-right: 70px;
  padding-left: 70px;
  cursor: default;
}

.image-modal > div {
  width: 100%;
  background-color: white;
  border-radius: 4px;
}

.image-modal-close {
  position: absolute;
  top: 30px;
  left: 30px;
}

.image-modal-close > button {
  font-size: 25px;
  color: white;
  border: 0;
  background-color: rgba(0, 0, 0, 0);
  cursor: pointer;
}

.image-modal-close > button:focus {
  outline: 0;
}

.image-modal-header {
  display: flex;
  justify-content: space-between;
  padding-top: 15px;
}

.image-modal-header > button {
  border-color: #d1d1d1;
}

.image-modal-body > div {
  text-align: center;
  padding: 15px 15px;
}

.image-modal-body > div > a {
  display: inline-block;
  position: relative;
  font-size: 25px;
}

.image-modal-body > div > a > i {
  position: absolute;
  top: 15px;
  right: 15px;
  z-index: 2;
}

.image-modal-body > div > a > img {
  width:100%;
  margin: 0 auto;
}


.container {
  max-width: 1280px;
  padding-top: 50px;
  margin:0 auto;
}

.row {
  display: flex;
  flex-wrap: wrap;
  padding: 0 10px;
}

.column {
  flex: 30% 1;
  width: 30%;
  padding: 0px 0px 15px 15px;
}

html,
body {
  width: 100%;
  height: 100%;
  padding: 0;
  margin: 0;
  font-family: 'Open Sans', sans-serif;
  font-size: 100%;
  background-color: #121212;
}

button:focus {
  outline: 0;
}

a,
a:visited,
a:active {
  color: inherit;
}

.hidden {
  visibility: hidden;
}

.visible {
  visibility: visible;
}

.body-modal-open {
  overflow: hidden;
}

@media (max-width: 860px) {
  html,
  body {
    font-size: 12px;
  }
}

@media (max-width: 600px) {
  html,
  body {
    font-size: 9px;
  }
}
